import React from 'react';


import { NavBar }  from   "../components/NavBar.js"
import { View }  from   "../components/View.js"
import {  VLayout}  from   "../components/VLayout.js"
import {  Container}  from   "../components/Container.js"

import { Checkbox }  from   "../components/Checkbox.js"

import { BaseController }  from   "./common/BaseController.js"
//
export class CheckboxExample extends BaseController {

    /*
     *  :  state
     */
    constructor(props) {
        super(props);

    }
    /*
     *  : 
     *  : super.componentDidMount();  
     */
    componentDidMount() {
        super.componentDidMount();
    }
    /*
     *  :  
     *  :  AB  B A
     */
    componentResume () {
        super.componentResume();
    }
    /*
     *  :  
     *  :  AB   A
     */
    componentPause () {
        super.componentPause();
    }
    /*
     *  :  
     */
    componentWillUnmount () {
        super.componentWillUnmount();
    }


    onTouchTapBack(e) {
        this.props.navigationController.popView()
    }

    onTouchTap(e) {
        alert('check');
        e.preventDefault();

    }

    render() {

        return (
            <Container >
                <NavBar title="CheckboxExample" onLeftTouchTap={this.onTouchTapBack.bind(this)}>
                </NavBar>
                <VLayout isFill={true} bgColor="#00f">
                    <Checkbox

                        h="30"
                        fontSize="20"
                        fontColor="#F00"
                        label=''
                        iconColor="#000"
                        space="30"
                        onTouchTap={this.onTouchTap.bind(this)}>
                    </Checkbox>

                    <Checkbox
                        ml="30"
                        label=''
                        space="">
                    </Checkbox>
                </VLayout>

            </Container>

        );
    }
}





