import React from 'react';


import { NavBar }  from   "../components/NavBar.js"
import { View }  from   "../components/View.js"
import {  VLayout}  from   "../components/VLayout.js"

import {  HLayout}  from   "../components/HLayout.js"
import {  Container}  from   "../components/Container.js"
import { ButtonGroup }  from   "../components/ButtonGroup.js"

import { BaseController }  from   "./common/BaseController.js"
//
export class ButtonGroupExample extends BaseController{

    /*
     *  :  state
     */
    constructor(props) {
        super(props);

    }
    /*
     *  : 
     *  : super.componentDidMount();  
     */
    componentDidMount() {
        super.componentDidMount();
    }
    /*
     *  :  
     *  :  AB  B A
     */
    componentResume () {
        super.componentResume();
    }
    /*
     *  :  
     *  :  AB   A
     */
    componentPause () {
        super.componentPause();
    }
    /*
     *  :  
     */
    componentWillUnmount () {
        super.componentWillUnmount();
    }


    onTouchTapBack(e) {
        this.props.navigationController.popView()
    }

    onSwich(e,index) {
        console.log('buttongroup', index);
    }

    render() {



        return (

            <Container >

                <NavBar title="ButtonGroupExample" onLeftTouchTap={this.onTouchTapBack.bind(this)}>
                </NavBar>
                <VLayout isFill={true} >


                    <HLayout >
                        <ButtonGroup mt='10'   ml='10' borderColor = '#0f0' selBgColor="#0f0" items = {['','','',''] } fontSize='18' p="10" onSwich={this.onSwich.bind(this)}>

                        </ButtonGroup>
                    </HLayout>

                </VLayout>

            </Container>

        );
    }
}





