import React from 'react';


import { NavBar }  from   "../components/NavBar.js"
import { View }  from   "../components/View.js"
import {  VLayout}  from   "../components/VLayout.js"

import {  HLayout}  from   "../components/HLayout.js"


import {  Container}  from   "../components/Container.js"
import { Button }  from   "../components/Button.js"


import { Input }  from   "../components/Input.js"



import { Textarea }  from   "../components/Textarea.js"

//
import { BaseController }  from   "./common/BaseController.js"
export class InputExample extends BaseController {

    /*
     *  :  state
     */
    constructor(props) {
        super(props);

    }
    /*
     *  : 
     *  : super.componentDidMount();  
     */
    componentDidMount() {
        super.componentDidMount();
    }
    /*
     *  :  
     *  :  AB  B A
     */
    componentResume () {
        super.componentResume();
    }
    /*
     *  :  
     *  :  AB   A
     */
    componentPause () {
        super.componentPause();
    }
    /*
     *  :  
     */
    componentWillUnmount () {
        super.componentWillUnmount();
    }

    onTouchTapBack(e) {
        this.props.navigationController.popView()
    }

    onChange(e,v) {
     console.log('oooooooooooo',v);
        // this.props.navigationController.popView()

        e.preventDefault();

    }

    render() {

        return (

            <Container >

                <NavBar title="InputExample" onLeftTouchTap={this.onTouchTapBack.bind(this)}>
                </NavBar>
                <VLayout isFill={true} bgColor="#FFf">


                    <HLayout  >
                        <Input  type="search" mt="20" fontSize="18" ml="10" placeholder=''
                                onChange={this.onChange.bind(this)}>

                        </Input>
                    </HLayout>

                    <HLayout >
                        <Textarea mt="20" fontSize="18" ml="10" placeholder=''
                                onChange={this.onChange.bind(this)}>

                        </Textarea>
                    </HLayout>

                </VLayout>

            </Container>

        );
    }
}





