import React from 'react';

import assign from 'object-assign'
import classnames from 'classnames';

import { View }  from   "./View.js"

import Config from   "./common/Config.js"
import Utils from   "./common/Utils.js"


import IOCheck from 'react-icons/lib/io/ios-checkmark-outline';
import IOChecked from 'react-icons/lib/io/ios-checkmark';
import { HLayout }  from   "../components/HLayout.js"

export class Checkbox extends React.Component {



    static propTypes = {
        fontSize: React.PropTypes.string,       // 
        fontColor: React.PropTypes.string,      // 
        iconColor: React.PropTypes.string,      // 
        label: React.PropTypes.string,          // 
        space:React.PropTypes.string,           // 
        checked:React.PropTypes.bool,           // 
        onTouchTap:React.PropTypes.func         // touch

    };

    static defaultProps = {
        fontSize: Config.CheckboxFontSize,       // 
        fontColor: Config.CheckboxFontColor,      // 
        iconColor:Config.CheckboxIconColor,       // 
        label: '',      // 
        space:'',      // 
        h:Config.CheckboxHeight,
        checked:false,     // 
    };


    constructor(props) {

        super(props);
        const {
            fontSize,
            fontColor,
            iconColor,
            label,
            space,
            checked,
            h
            } = this.props;

        this.state = {
            fontSize: fontSize,
            fontColor: fontColor,
            iconColor: iconColor,
            label: label,
            space: space,
            h: h,
            checked :checked
        };


    };


    /*
     * :  
     * :  null
     * :  null
     */

    onTouchTap(e) {
        this.setState({checked: !this.state.checked});
        var onTouchTap = this.props.onTouchTap;
        if (onTouchTap) {
            onTouchTap(e);
        }
        e.preventDefault();
    }

    render() {
        const {
            h,
            fontSize,
            fontColor,
            iconColor,       // 
            label,           // 
            space,           // 
            ...props
            } = this.props;

        var IOCheckIcon;
        if (this.state.checked == false) {
            IOCheckIcon = IOCheck
        } else {
            IOCheckIcon = IOChecked
        }


        return (

            <HLayout flexDirection="row" justifyContent="flex-start"  {...props} onTouchTap={this.onTouchTap.bind(this)}>
                <IOCheckIcon color={this.state.iconColor} size={Utils.sizeUnit(this.state.h)}/>
                <View ml={this.state.space} h={this.state.h} fontSize = {this.state.fontSize} fontColor={this.state.fontColor}> {this.state.label}  </View>
            </HLayout >




        );
    }
}





