import React from 'react';

import { BaseController }  from   "./common/BaseController.js"
import { NavBar }  from   "../components/NavBar.js"
import { View }  from   "../components/View.js"
import {  VLayout}  from   "../components/VLayout.js"

import {  HLayout}  from   "../components/HLayout.js"


import {  Container}  from   "../components/Container.js"
import { Button }  from   "../components/Button.js"


import { Switch }  from   "../components/Switch.js"


//
export class SwitchExample extends BaseController {

    /*
     *  :  state
     */
    constructor(props) {
        super(props);

    }
    /*
     *  : 
     *  : super.componentDidMount();  
     */
    componentDidMount() {
        super.componentDidMount();
    }
    /*
     *  :  
     *  :  AB  B A
     */
    componentResume () {
        super.componentResume();
    }
    /*
     *  :  
     *  :  AB   A
     */
    componentPause () {
        super.componentPause();
    }
    /*
     *  :  
     */
    componentWillUnmount () {
        super.componentWillUnmount();
    }



    onTouchTapBack(e) {
        this.props.navigationController.popView()
    }

    onSwitch(e, index) {
        console.log('buttongroup', index);

    }

    render() {


        return (

            <Container >

                <NavBar title="SwitchExample" onLeftTouchTap={this.onTouchTapBack.bind(this)}>
                </NavBar>
                <VLayout isFill={true} >


                    <Switch mt='10' ml='10' onSwitch={this.onSwitch.bind(this)} >

                    </Switch>


                </VLayout>

            </Container>

        );
    }
}





