import React from 'react';

import { BaseController }  from   "./common/BaseController.js"
import { NavBar }  from   "../components/NavBar.js"
import { View }  from   "../components/View.js"
import {  VLayout}  from   "../components/VLayout.js"
import {  Container}  from   "../components/Container.js"

import { Radio }  from   "../components/Radio.js"


//
export class RadioExample extends BaseController {

    /*
     *  :  state
     */
    constructor(props) {
        super(props);

    }
    /*
     *  : 
     *  : super.componentDidMount();  
     */
    componentDidMount() {
        super.componentDidMount();
    }
    /*
     *  :  
     *  :  AB  B A
     */
    componentResume () {
        super.componentResume();
    }
    /*
     *  :  
     *  :  AB   A
     */
    componentPause () {
        super.componentPause();
    }
    /*
     *  :  
     */
    componentWillUnmount () {
        super.componentWillUnmount();
    }


    onTouchTapBack(e) {
        this.props.navigationController.popView()
    }

    onTouchTap(e) {
        alert('button');
        // this.props.navigationController.popView()

        e.preventDefault();

    }

    render() {

        return (

            <Container >

                <NavBar title="RadioExample" onLeftTouchTap={this.onTouchTapBack.bind(this)} >
                </NavBar>
                <VLayout isFill={true} bgColor="#fff">
                    <Radio label='' >

                    </Radio>

                    <Radio h="30" fontSize="16" fontColor="000" label='' iconColor="#000"
                           space="">

                    </Radio>
                </VLayout>

            </Container>

        );
    }
}





