import React from 'react';


import { NavBar }  from   "../components/NavBar.js"
import { View }  from   "../components/View.js"
import {  VLayout}  from   "../components/VLayout.js"
import {  Container}  from   "../components/Container.js"

import { Button }  from   "../components/Button.js"


import { BaseController }  from   "./common/BaseController.js"
//
export class ButtonExample extends BaseController {

    /*
     *  :  state
     */
    constructor(props) {
        super(props);

    }
    /*
     *  : 
     *  : super.componentDidMount();  
     */
    componentDidMount() {
        super.componentDidMount();
    }
    /*
     *  :  
     *  :  AB  B A
     */
    componentResume () {
        super.componentResume();
    }
    /*
     *  :  
     *  :  AB   A
     */
    componentPause () {
        super.componentPause();
    }
    /*
     *  :  
     */
    componentWillUnmount () {
        super.componentWillUnmount();
    }


    onTouchTapBack(e) {
        this.props.navigationController.popView()
    }
    onTouchTap(e) {
      //  alert('button');
         this.props.navigationController.popView();

        e.preventDefault();

    }

    componentWillUnmount () {
        console.log("ButtonExample ----- componentWillUnmount!");
    }

    render() {

        console.log("ButtonExample ----- render!");
        return (

            <Container >

                <NavBar title="ButtonExample" onLeftTouchTap={this.onTouchTapBack.bind(this)}>
                </NavBar>
                <VLayout isFill ={true} bgColor="#00f">

                    <Button    ml="10" mt="10" w="100" h="40" bgColo1r="#ff0" fontColor1="#000" onTouchTap={this.onTouchTap.bind(this)}>
                          
                    </Button>
                    <Button ml="10" mt="10" w="100" h="40">
                        
                    </Button>
                </VLayout>

            </Container>

        );
    }
}





